import React, { useEffect, useState } from 'react';
// import Navbar from './components/Header/Navbar/Navbar';
import Banner from './components/Header/Banner/Banner';
import Main from './components/Main/Main';
import LoadingScreen from './components/SharedUI/LoadingScreen';
import styled from 'styled-components';
import AOS from 'aos';
import HOCSidebar from './components/Sidebar/HOCSidebar';

const TopWrapper = styled.div`
  overflow: hidden;
  height: 100vh;
  width: 100vw;
  box-sizing: border-box;
`;

function App() {
  const [isLoading, setIsLoading] = useState(true);
  const [isSmallScreen, setIsSmallScreen] = useState(true);
  console.log('isSmallScreen', isSmallScreen);

  const attachChecker = () => {
    checkScreenSize();
    window.addEventListener('resize', () => {
      checkScreenSize();
    });
  };

  const checkScreenSize = () => {
    const TABLET_BREAKPOINT = 768;
    if (window.innerWidth >= TABLET_BREAKPOINT) {
      return setIsSmallScreen(false);
    }
    return setIsSmallScreen(true);
  };

  useEffect(() => {
    attachChecker();
    setTimeout(() => {
      setIsLoading(false);
      AOS.refreshHard();
    }, 2000); // 3s is too long
    // eslint-disable-next-line
  }, []);

  return (
    <>
      {isLoading ? (
        <TopWrapper>
          <LoadingScreen shown={isLoading} />
          {/* Loading Banner when loading to avoid load time */}
          <Banner />
        </TopWrapper>
      ) : (
        <>
          <Banner />
          {isSmallScreen ? (
            <HOCSidebar>
              <Main />
            </HOCSidebar>
          ) : (
            <>
              <Main />
              <HOCSidebar />
            </>
          )}
        </>
      )}
    </>
  );
}

export default App;
