import React from 'react';
import styled, { keyframes } from 'styled-components';
import BannerBackground from './../../../assets/images/first-page-banner.jpg';
import BannerText from './BannerText';
import { Icon } from 'semantic-ui-react';
import { Parallax } from 'react-parallax';

const Wrapper = styled.div`
  /* background: url(${BannerBackground}) no-repeat center/cover; */
  /* background-attachment: fixed; */
  width: 100vw;
  height: 100vh;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
`;

const arrowAnimation = keyframes`
  0% {
    transform: translate(0, 0);
  }
  50% {
    transform: translate(0, 10px);
  }
  100% {
    transform: translate(0, 0);
  }
`;

const StyledArrow = styled(Icon)`
  position: absolute;
  bottom: 1.5em;
  animation: ${arrowAnimation} 2s infinite;
`;

function Banner() {
  return (
    <Parallax
      bgImage={BannerBackground}
      bgImageAlt='Universe banner'
      strength={-220}
    >
      <Wrapper>
        <BannerText />
        <StyledArrow name='angle down' size='big' color='orange' />
      </Wrapper>
    </Parallax>
  );
}

export default Banner;
