import React from 'react';
import styled from 'styled-components';
import './styles.scss';
import { Icon } from 'semantic-ui-react';
import { useEffect } from 'react';
import { useState } from 'react';

const StyledBannerText = styled.div`
  border: 6px double white;
  text-align: center;
  /* transform: translateY(-10px); */
  padding: 2rem;
  padding-bottom: 0;
  background-color: rgba(93, 81, 121, 0.2);
  h1 {
    font-family: 'Baloo Tamma 2', cursive;
    font-size: 8vmin;
    padding-bottom: 0.5rem;
    border-bottom: 2px solid white;
  }
`;

const JobTitle = styled.div`
  text-align: center;
  /* text-transform: uppercase; */
  font-weight: 600;
  font-size: 4vmin;
  font-family: 'Baloo Tamma 2', cursive;
`;

const IconWrapper = styled.div`
  padding: 1em 0;
  .icon {
    /* font-size: 0.9rem; */
    font-size: 3.2vmin;
    transition: transform 0.5s;
  }
  .icon:hover {
    transform: translateY(-4px);
  }
`;

function BannerText() {
  const [jobTitle, setJobTitle] = useState('Web Developer');

  useEffect(() => {
    setTimeout(execTypingEffect, 500);
    // eslint-disable-next-line
  }, []);

  const execTypingEffect = async () => {
    let pos = 0;
    const speed = 200;
    let typingText = '';
    await setTimeout(() => {}, 5000);
    let timer = await setInterval(() => {
      if (pos < jobTitle.length) {
        typingText += jobTitle.charAt(pos);
        setJobTitle(typingText);
        pos++;
      } else {
        clearInterval(timer);
      }
    }, speed);
  };

  return (
    <StyledBannerText data-aos='fade-up' data-aos-duration='800' id='banner'>
      <h1 data-aos='zoom-in-up' data-aos-duration='1500'>
        Hi, I&apos;m <span>Tô Vủ Phong</span>
      </h1>
      <JobTitle data-aos='fade-up' data-aos-duration='1200'>
        <span>{jobTitle}</span>
      </JobTitle>
      <IconWrapper>
        <Icon name='html5' color='orange' />
        <Icon name='css3 alternate' color='blue' />
        <Icon name='js' color='yellow' />
        <Icon name='react' color='teal' />
        <Icon name='node js' color='green' />
      </IconWrapper>
    </StyledBannerText>
  );
}

export default BannerText;
